.sider-nav {
  display: flex;
  flex-direction: column;
  min-height: 540px;
  justify-content: space-between;
}

.sider-nav .sider-nav-item {
  width: 165px;
  position: relative;
  line-height: 36px;
  color: #999;
  padding-left: 16px;
  display: flex;
  align-items: center;
  border-radius: 4px;
  font-size: 16px;
}

.sider-nav .sider-nav-item:hover {
  background:rgba(58,146,249,.4);
  color: #fff;
}

.sider-nav .sider-nav-item .sider-item-icon {
  margin-right: 4px;
}

.sider-nav .sider-nav-item.active {
  color: #3A92F9;
  font-weight: 800;
  /* box-shadow:0px 5px 15px rgba(58,146,249,0.5); */
  /* margin: 8px 0; */
  /* margin-bottom: 16px; */
}

.sider-nav .sider-nav-item.active:hover {
  color: #FFF;
}

.sider-nav .sider-nav-item:nth-child(6):after, 
.sider-nav .sider-nav-item:nth-child(9):after {
  content: '';
  width: 32px;
  height: 1px;
  background: #cecece;
  position: absolute;
  bottom: -18px;
  left: 16px;
}

.sider-nav .sider-nav-item:nth-child(1):after {content: '';
  width: 32px;
  height: 2px;
  background: #cecece;
  position: absolute;
  bottom: -18px;
  left: 16px;
}

.sider-nav .sider-nav-item:nth-child(1), 
.sider-nav .sider-nav-item:nth-child(6),
.sider-nav .sider-nav-item:nth-child(9){
  margin-bottom: 32px;
}

.side-icon-item {
  margin-right: 12px;
  transform: scale(1.1);
}