}

.title-bar-public {
  height: 128px;
  background: #fff;
  width: 100vw;
  position: absolute;
  top: 60px;
  left: 0;
  padding: 0 16%;
  display: flex;
  align-items: center;
}

.group-detail .group-special {
  padding-top: 21px;
}

.title-bar-public .avatar {
  width: 63px;
  height: 63px;
  background: #f5f5f5;
  border-radius: 100%;
  margin-right: 21px;
  text-align: center;
  line-height: 63px;
  font-size: 28px;
  font-weight: 800;
}

.title-bar-public .avatar img {
  width: 60%;
}

.title-bar-public .title {
  display: flex;
  vertical-align: middle;
  margin-top: 6px;
}

.title-bar-public .title h3 {
  font-size: 18px;
  line-height: 24px;
  margin: 0 48px 12px 0;
}

.title-bar-public .collectBtn, .title-bar-public .defaultBtn {
  display: inline-block;
  padding: 0 16px;
  border-radius: 4px;
  line-height: 24px;
  height: 24px;
}

.title-bar-public .collectBtn {
  border: 1px solid #3A92F9;
  color: #3A92F9;
}

.group-detail .main-content {
  margin-top: 136px;
  padding: 21px 0 0;
}

.group-detail .nav {
  width: 420px;
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
}

.nav .group-menu-item {
  color: #666;
  position: relative;
  line-height: 40px;
  transition-duration: .5s;
}

.nav .group-menu-item:hover, .nav .group-menu-item.active {
  color: #3A92F9;
}

.nav .group-menu-item:after {
  content: '';
  transition-duration: .5s;
  transform-origin: right bottom;
}

.nav .group-menu-item.active:after, .nav .group-menu-item:hover:after {
  content: '';
  position: absolute;
  width: 150%;
  height: 4px;
  background-color: #3A92F9;
  left: 50%;
  bottom: 0;
  transform: translate(-50%, 0);
}

.nav .group-menu-item.active:hover {
  color: #666;
}

