.analysis {
  min-height: 600px;
}

.analysis .firstData {
  margin: 21px 0 21px;
}

.firstData .card {
  padding: 21px;
  margin-right: 21px;
  height: 180px;
  position: relative;
  overflow: hidden;
  background: #fff;
  border: 1px solid #eee;
}

.firstData .card:first-child {
  flex: 1;
}

.firstData .card:nth-child(2), .firstData .card:nth-child(3) {
  align-items: flex-start;
  width: 400px;
  display: flex;
  flex-direction: column;
  padding-right: 0;
}

.firstData .card:nth-child(3) {
  width: 440px;
}

.firstData .card:nth-child(2) .box, .firstData .card:nth-child(3) .box {
  width: 160px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}

.firstData .card:nth-child(2) .chart_one,
.firstData .card:nth-child(3) .chart_one {
  flex: 1;
  position: absolute;
  right: -40px;
  top: 0;
  width: 300px;
}

.firstData .card:nth-child(3) .chart_one {
  right: 0;
} 

.firstData .card:last-child {
  margin: 0;
}

.card:nth-child(1) {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 150px;
}

.card .title {
  font-size: 20px;
  text-align: left;
}

.card .numberCount {
  font-size: 32px;
}

.card .desc-paragraph span {
  display: block;
}

.bottomWrap {
  background: #fff;
}

.bottomWrap .placeHolder {
  margin: 0;
}

.bottomWrap .placeHolder img {
  width: 50%;
}

.analysis-content {
  margin-bottom: 32px;
}

.mainWrap-content {
  background: #1890ff;
  color: #fff;
  background-image: url(https://gw.alipayobjects.com/zos/basement_prod/94072183-916e-4dbf-b328-aea3d8988949.svg);
  background-repeat: no-repeat;
  background-size: 50%;
  background-position: 100% 50px;
  border-radius: 10px;
  min-height: 244px;
  width: 50%;
  height: 300px;
  padding: 24px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.mainWrap-content .title {
  display: flex;
  justify-content: space-between;
  text-align: left;
  width:100%;
}

.mainWrap-content .title h1 {
  font-size: 32px;
  margin: 0;
  color: #fff;
  font-weight: 800;
}

.mainWrap-content .list-content {
  display: flex;
}

.mainWrap-content .list-content dl {
  margin: 0;
  width: 160px;
}

.mainWrap-content .list-content dl:not(:first-child) {
  border-left: 1px solid rgba(255,255,255, .4);
  padding-left: 20px;
}

.mainWrap-content .list-content dl dt {
  font-size: 28px;
}

.heightChart {
  background: #fff;
  padding: 24px;
  height: 300px;
  border-radius: 4px;
  flex: 1;
  margin-left: 24px;
  border: 1px solid #eee;
}