.task-board {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.task-board .logo {
  margin-right: 21px;
}

.task-board .header {
  height: 54px;
  background: #fff;
  box-shadow: 0px 2px 7px rgba(0,0,0, .1);
  width: 100%;
  align-items: center;
  padding: 0 32px;
}

.task-board .header h3 {
  line-height: 54px;
  margin: 0;
}

.header .backBtn {
  color: #666;
}

.header .backBtn:hover {
  color: #3A92F9;
}

.task-board .main-content {
  flex: 1;
  overflow-y: hidden;
  overflow-x: auto;
  width: 100%;
  padding: 26px 32px 12px;
  white-space: nowrap;
}

.task-board .main-content::-webkit-scrollbar {
  height: 10px;
}

.task-board .main-content::-webkit-scrollbar, 
.main-content .task-columnItem::-webkit-scrollbar {
  /* display: none; */
  width: 10px;
  border-radius: 30px;
}

.task-board .main-content::-webkit-scrollbar-thumb, 
.main-content .task-columnItem::-webkit-scrollbar-thumb {
  background: #aaa;
  border-radius: 30px;
}

.main-content .task-columnItem:last-child {
  margin-right: 0;
}


.tags {
  padding: 0 12px;
  display: block;
  line-height: 28px;
  border-radius: 4px;
  font-size: 12px;
}

.finish {
  color: #fff;
  background: #3A92F9;
}

.doing {
  color: #999;
  background: rgba(245,245,245,1);
}

.task-board .search-bar {
  width: 500px !important;
}


